/* login page */
.login-page {
    .am-form {
        padding-top: 30px;
        width: 90%;
        margin:auto;
    }
    .get-smscode {
        position: absolute;
        right:0.5em;
        top: 50%;
        display: block;
        margin-top: -.5em;
        line-height: 1;
        z-index: 2;
    }
    .am-btn {
        display: block;
        width:100%;
        color: #fff;
    }
    .wechat {
        display: inline-block;
        width: 36px;
        height: 36px;
        border-radius: 36px;
        background: #2ed441;
        text-align: center;
        line-height: 36px;
        i {
            font-size: 20px;
            color: #fff;
        }
    }
    .line {
        border-top:1px solid #ddd;
        margin:40px auto 0 auto;
        width:80%;
        height: 30px;
        position: relative;
        span {
            display: block;
            width: 100px;
            height: 30px;
            text-align: center;
            padding: 0 10px;
            background: #fff;
            position: absolute;
            top:-15px;
            left:50%;
            line-height: 30px;
            margin-left:-50px;
        }
    }
}